'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import { createClient } from '@/utils/supabase/client'
import { Plus, Film, Trash2, Loader2, Calendar, ArrowRight } from 'lucide-react'
import { toast, Toaster } from 'sonner'
import Link from 'next/link'

// ÂÆö‰πâÈ°πÁõÆÁ±ªÂûã
type Project = {
  id: string
  title: string
  description: string | null
  updated_at: string
}

export default function CineFlowDashboard() {
  const router = useRouter()
  const supabase = createClient()
  
  const [projects, setProjects] = useState<Project[]>([])
  const [loading, setLoading] = useState(true)
  const [isCreating, setIsCreating] = useState(false)

  // 1. Âä†ËΩΩÈ°πÁõÆÂàóË°®
  useEffect(() => {
    fetchProjects()
  }, [])

  const fetchProjects = async () => {
    try {
      // Â∞ùËØïËé∑ÂèñÁî®Êà∑Ôºå‰ΩÜÂ∞±ÁÆóËé∑ÂèñÂ§±Ë¥•‰πü‰∏çË¶ÅË∑≥ËΩ¨
      const { data: { user } } = await supabase.auth.getUser()
      
      // üõë ‰πãÂâçÁöÑÂº∫Âà∂Ë∑≥ËΩ¨ÈÄªËæëÂ∑≤ÁßªÈô§ÔºåÈò≤Ê≠¢Ê≠ªÂæ™ÁéØ
      // if (!user) router.push('/login') 

      const { data, error } = await supabase
        .from('projects')
        .select('*')
        .order('updated_at', { ascending: false })

      if (error) throw error
      setProjects(data || [])
    } catch (error) {
      console.error('Âä†ËΩΩÂ§±Ë¥•:', error)
      // toast.error('Êó†Ê≥ïÂä†ËΩΩÈ°πÁõÆÂàóË°®') // ÂèØÈÄâÔºöÂ±èËîΩÊä•Èîô‰ª•ÂÖçÂ§™Âêµ
    } finally {
      setLoading(false)
    }
  }

  // 2. Êñ∞Âª∫È°πÁõÆ (Ê†∏ÂøÉ‰øÆÊîπÔºöÁ°¨ÁºñÁ†Å ID)
  const handleCreateProject = async () => {
    try {
      setIsCreating(true)
      
      // =========================================================
      // üõë ÁªïËøá Auth Ê£ÄÊü•Ôºå‰ΩøÁî®Á°¨ÁºñÁ†Å ID
      // =========================================================
      
      // üëáüëáüëá „ÄêÈáçË¶Å„ÄëËØ∑Êää‰∏ãÈù¢ÁöÑÂ≠óÁ¨¶‰∏≤Êç¢Êàê‰Ω†ÁöÑÁúüÂÆû User UUID üëáüëáüëá
      const userId = 'cec386b5-e80a-4105-aa80-d8d5b8b0a9bf'; 
      
      // =========================================================

      if (userId.includes('ËØ∑Âú®ËøôÈáå')) {
        toast.error('ËØ∑ÂÖàÂú®‰ª£Á†ÅÈáåÂ°´ÂÖ•‰Ω†ÁöÑ User UUIDÔºÅ');
        return;
      }

      // ÂàõÂª∫‰∏Ä‰∏™ÈªòËÆ§È°πÁõÆ
      const { data, error } = await supabase
        .from('projects')
        .insert({
          user_id: userId, // <--- ‰ΩøÁî®Âº∫Âà∂ ID
          title: 'Êú™ÂëΩÂêçÂàÜÈïúÈ°πÁõÆ',
          description: 'ËøôÊòØ‰∏Ä‰∏™Êñ∞ÁöÑÂàõÊÑè...',
          status: 'draft'
        })
        .select()
        .single()

      if (error) throw error

      toast.success('È°πÁõÆÂàõÂª∫ÊàêÂäüÔºÅ')
      // Ë∑≥ËΩ¨Âà∞ÁºñËæëÂô®È°µÈù¢
      router.push(`/tools/cineflow/${data.id}`)

    } catch (error: any) {
      console.error('ÂàõÂª∫Â§±Ë¥•:', error)
      toast.error(error.message || 'ÂàõÂª∫È°πÁõÆÂ§±Ë¥•')
    } finally {
      setIsCreating(false)
    }
  }

  // 3. Âà†Èô§È°πÁõÆ
  const handleDelete = async (id: string, e: React.MouseEvent) => {
    e.preventDefault() 
    if (!confirm('Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™È°πÁõÆÂêóÔºüÂà†Èô§ÂêéÊó†Ê≥ïÊÅ¢Â§ç„ÄÇ')) return

    try {
      const { error } = await supabase.from('projects').delete().eq('id', id)
      if (error) throw error
      
      setProjects(prev => prev.filter(p => p.id !== id))
      toast.success('Â∑≤Âà†Èô§')
    } catch (error) {
      toast.error('Âà†Èô§Â§±Ë¥•')
    }
  }

  return (
    <div className="min-h-screen bg-black text-white p-6 md:p-12">
      <Toaster position="top-center" richColors />
      
      <div className="max-w-6xl mx-auto">
        {/* Â§¥ÈÉ® */}
        <div className="flex items-center justify-between mb-12">
          <div>
            <h1 className="text-3xl font-bold bg-gradient-to-r from-purple-400 to-blue-500 bg-clip-text text-transparent mb-2">
              CineFlow Â∑•‰ΩúÂè∞
            </h1>
            <p className="text-gray-400">ÁÆ°ÁêÜ‰Ω†ÁöÑ AI ÂàÜÈïúÈ°πÁõÆ</p>
          </div>
          
          <div className="flex gap-4 items-center">
            {/* Êñ∞Â¢ûÔºöÊâãÂä®ÁôªÂΩïÂÖ•Âè£ (Èò≤Ê≠¢Êó†Ë∑ØÂèØÈÄÄ) */}
            <Link href="/login" className="text-gray-500 hover:text-white text-sm transition-colors">
               ÁôªÂΩï / ÂàáÊç¢Ë¥¶Âè∑
            </Link>

            <button
              onClick={handleCreateProject}
              disabled={isCreating}
              className="bg-white text-black hover:bg-gray-200 px-6 py-2.5 rounded-full font-bold flex items-center gap-2 transition-all disabled:opacity-50"
            >
              {isCreating ? <Loader2 className="animate-spin w-4 h-4"/> : <Plus className="w-4 h-4"/>}
              Êñ∞Âª∫È°πÁõÆ
            </button>
          </div>
        </div>

        {/* ÂàóË°®Âå∫Âüü */}
        {loading ? (
          <div className="flex justify-center py-20">
            <Loader2 className="animate-spin text-purple-500 w-8 h-8" />
          </div>
        ) : projects.length === 0 ? (
          // Á©∫Áä∂ÊÄÅ
          <div className="text-center py-20 border border-white/10 rounded-2xl bg-white/5 border-dashed">
            <div className="w-16 h-16 bg-white/10 rounded-full flex items-center justify-center mx-auto mb-4">
              <Film className="w-8 h-8 text-gray-400" />
            </div>
            <h3 className="text-xl font-bold mb-2">ËøòÊ≤°ÊúâÈ°πÁõÆ</h3>
            <p className="text-gray-500 mb-6">ÂàõÂª∫‰∏Ä‰∏™Êñ∞È°πÁõÆÔºåÂºÄÂßã‰Ω†ÁöÑÂØºÊºî‰πãË∑Ø</p>
            <button
              onClick={handleCreateProject}
              className="text-purple-400 hover:text-purple-300 font-bold flex items-center gap-1 mx-auto"
            >
              Á´ãÂç≥ÂàõÂª∫ <ArrowRight className="w-4 h-4" />
            </button>
          </div>
        ) : (
          // È°πÁõÆÁΩëÊ†º
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
            {projects.map(project => (
              <Link 
                href={`/tools/cineflow/${project.id}`} 
                key={project.id}
                className="group relative bg-[#151515] border border-white/10 rounded-xl p-6 hover:border-purple-500/50 transition-all hover:-translate-y-1"
              >
                <div className="flex justify-between items-start mb-4">
                  <div className="w-10 h-10 bg-purple-900/30 rounded-lg flex items-center justify-center text-purple-400 group-hover:bg-purple-600 group-hover:text-white transition-colors">
                    <Film className="w-5 h-5" />
                  </div>
                  <button 
                    onClick={(e) => handleDelete(project.id, e)}
                    className="text-gray-600 hover:text-red-500 p-2 transition-colors"
                    title="Âà†Èô§È°πÁõÆ"
                  >
                    <Trash2 className="w-4 h-4" />
                  </button>
                </div>

                <h3 className="text-xl font-bold mb-2 group-hover:text-purple-400 transition-colors line-clamp-1">
                  {project.title}
                </h3>
                <p className="text-gray-500 text-sm mb-6 line-clamp-2 min-h-[40px]">
                  {project.description || 'ÊöÇÊó†ÊèèËø∞'}
                </p>

                <div className="flex items-center text-xs text-gray-600 pt-4 border-t border-white/5">
                  <Calendar className="w-3 h-3 mr-1" />
                  <span>Êõ¥Êñ∞‰∫é {new Date(project.updated_at).toLocaleDateString()}</span>
                </div>
              </Link>
            ))}
          </div>
        )}
      </div>
    </div>
  )
}
